import { FaPlusCircle, FaTimesCircle } from 'react-icons/fa'

const Button = ({ onClick, showAddSection }) => {
    return(
        <>
        {showAddSection ? <FaTimesCircle onClick={onClick} className="addBtn"/> : <FaPlusCircle onClick={onClick} className="addBtn clr"/>}
        </>
    )
}

export default Button;