import Button from "./Button";
import { useLocation } from "react-router-dom";

const Header = ({ title, addTask, showAddSection }) => {
    const location = useLocation()
    return (
        <header className="conatiner">
            <h3>React {title}</h3>
            {location.pathname === '/' && (<Button onClick={addTask} showAddSection={showAddSection}/>)}
        </header>
    )
}

export default Header;