import { FaTrash } from 'react-icons/fa';

const Task = ({ task, onDelete, onHighLight }) => {
    return(
        // <div className={`taskContainer ${task.flag? 'flag': ''}`}
        //<div className={task.flag? 'flag taskContainer': 'taskContainer'}
        <div className={'taskContainer ' + (task.flag? 'flag': '')}
        onDoubleClick={() => onHighLight(task.id)}>
            <h5>{task.title}</h5><FaTrash className="deleteTask" onClick={() => onDelete(task.id)}/>
        </div>
    )
}

export default Task;