import { useState } from "react";

const AddTask = ({ saveTask }) => {
    const [title, setTitle] = useState('');
    const [flag, setFlag] = useState(false);

    const onAddSubmit = (e) => {
        e.preventDefault();

        if(!title) {
            alert('Please enter title');
            return;
        }
        saveTask({title, flag});
        setTitle('');
        setFlag(false);
    }

    return(
        <form onSubmit={onAddSubmit} className="formStyle">
            <label>Title </label>
            <input placeholder="enter task title" type="text" value={title} onChange={(e) => setTitle(e.target.value)}></input>
            <label style={{marginLeft: '10px'}}>Reminder</label>
            <input type="checkbox" checked={flag} onChange={(e) => setFlag(e.currentTarget.checked)}/>
            <button type="submit" className="saveBtn">Save</button>
        </form>
    )
}

export default AddTask