import './App.css';
import { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'
import Header from './components/Header';
import Footer from './components/Footer';
import Tasks from './components/Tasks';
import AddTask from './components/AddTask';
import About from './components/About';

function App() {
  const [tasks, setTasks] = useState([])

  const [showAddSection, setShowAddSection] = useState(false);

  useEffect(() => {
    const getTask = async () => {
      const dataFromServer = await fetchTasks()
      setTasks(dataFromServer)
    }
    getTask()
  },[])

  //Fetch Data
  const fetchTasks = async () => {
    const res = await fetch('http://localhost:5000/tasks')
    const data = await res.json()
    return data
  }

  const fetchTask = async (id) => {
    const res = await fetch(`http://localhost:5000/tasks/${id}`)
    const data = await res.json()
    return data
  }

  // delete Task
  const deleteTask = async (id) => {
    await fetch(`http://localhost:5000/tasks/${id}`, {
      method: 'DELETE',
    })

    setTasks(tasks.filter((task) => 
      task.id !== id
    ))
  }
  //Highlight Task
  const highLightTask = async (id) => {
    const taskToToggle = await fetchTask(id)
    const updTask = {...taskToToggle, flag: !taskToToggle.flag}

    const res = await fetch(`http://localhost:5000/tasks/${id}`, {
      method: 'PUT',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(updTask)
    })

    const data = await res.json()
    setTasks(tasks.map((task) => 
      (task.id === id)? {...task, flag: data.flag}: task
    ))
  }

  //show add task
  const showAddTask = () => {
    setShowAddSection(!showAddSection);
  }

  const saveTask = async (task) => {
    const res = await fetch('http://localhost:5000/tasks', {
      method: 'POST',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify(task),
    })
    const data = await res.json()
    setTasks([...tasks, data])
  }

  return (
    <Router>
    <div className="App">
      <Header title="Task Tracker" addTask={showAddTask} showAddSection={showAddSection}/>
      {/* {(showAddSection)? <AddTask /> : ''} */}
      
      <Route path="/" exact render={(props) => (
        <>
        {showAddSection && <AddTask saveTask={saveTask}/>}
      <Tasks tasks={tasks} onDelete={deleteTask} 
      onHighLight={highLightTask}/>
        </>
      )} />
      <Route path="/about" component={About} />
      <Footer />
    </div>
    </Router>
  );
}

export default App;
